/* Hide scrollbar for Chrome, Safari, and Opera */
::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for Firefox */
html {
  scrollbar-width: none;
  overflow: -moz-scrollbars-none;
}

/* Hide scrollbar for IE and Edge */
 body {
  -ms-overflow-style: none;  
}

@keyframes fadeOut {
  0%   { opacity: 1; display: block; }
  99%  { opacity: 0; display: block; }
  100% { opacity: 0; display: none; }
}
.App-logo  {
  padding: 10px 20px;  /* reduce padding */
  color: rgb(99, 97, 97);  
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  margin:0 auto;  
  opacity: 1; /* Start with 90% opacity or any desired value */
  animation: fadeOut 5s forwards 1s; /* Start fade-out after 3 seconds, fade out over 1 second */
  pointer-events: none;
  height: 20vmin;
}

.osd {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  position: absolute;
  top: 0;
  left:0;
  width: 100%;
}

.block {
  display: flex;
  align-items: center;
  padding: 12px 24px;
  background-color: #282c34a2;   
  height: 80px; 
}
.block.left {
  padding-left: 96px;
}
.block.right {
  padding-right: 96px;
}